const state = {
  user: null
}

const getters = {
  user: state => state.user,
  isLoggedIn: state => (state.user !== null)
}

const mutations = {
  setUser: (state, user) => {
    state.user = user
  }
}

const actions = {
  setCurrentUser: ({commit}, payload) => {
    commit('setUser', payload)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



